@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Poppins:wght@200;600;700&display=swap");
/**
 * Mixin: breakpoint
 * This mixin allows you to apply styles based on different screen sizes.
 *
 * @param {String} $point - The breakpoint name. It can be 'xs', 'sm', 'md', 'lg', 'xl', or 'xxl'.
 *
 * Usage:
 * @include breakpoint(sm) {
 *   // Styles for small screens (min-width: 576px)
 * }
 *
 * @include breakpoint(md) {
 *   // Styles for medium screens (min-width: 768px)
 * }
 *
 * @include breakpoint(lg) {
 *   // Styles for large screens (min-width: 992px)
 * }
 */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
